import React, { useState } from 'react';
import { Clipboard, ClipboardCheck } from 'lucide-react';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { vscDarkPlus } from 'react-syntax-highlighter/dist/esm/styles/prism';

const snippets = [
  {
    label: 'JavaScript',
    lang: 'javascript',
    code: `<script src="https://adpay.com/sdk.js"></script>
<div id="adpay-container"></div>
<button id="pay">Payer</button>

<script>
  const adpay = new ADPay('public_key', {
    container: '#adpay-container'
  });

  adpay.init();

  document.getElementById('pay').onclick = () => {
    adpay.pay(tokenId)
      .then(response => {
        // success 
      })
      .catch(error => {
        // handle error 
      });
  };
</script>`
  },
  {
    label: 'React',
    lang: 'jsx',
    code: `import { useEffect } from 'react';

const ADPayComponent = () => {
  useEffect(() => {
    const adpay = new window.ADPay('public_key', {
      container: '#adpay-container',
    });
    adpay.init();
  }, []);

  const handlePayment = () => {
    adpay.pay(tokenId)
      .then(response => {
        // success 
      })
      .catch(error => {
        // handle error 
      });
  };

  return (
    <>
      <div id="adpay-container"></div>
      <button onClick={handlePayment}>Payer</button>
    </>
  );
};

export default ADPayComponent;`
  },
  {
    label: 'PHP',
    lang: 'php',
    code: `<?php
$ch = curl_init();

curl_setopt($ch, CURLOPT_URL, "https://api.adpay.com/v1/payments");
curl_setopt($ch, CURLOPT_POST, 1);
curl_setopt($ch, CURLOPT_POSTFIELDS, json_encode([
  "token" => "your_token_id",
  "amount" => 5000
]));
curl_setopt($ch, CURLOPT_HTTPHEADER, [
  "Authorization: Bearer YOUR_SECRET_KEY",
  "Content-Type: application/json"
]);

$response = curl_exec($ch);
curl_close($ch);

// Handle response...
?>`
  }
];

const CodeBlockWithTabs = () => {
  const [activeTab, setActiveTab] = useState(0);
  const [copied, setCopied] = useState(false);

  const currentCode = snippets[activeTab];

  const copyCode = async () => {
    await navigator.clipboard.writeText(currentCode.code);
    setCopied(true);
    setTimeout(() => setCopied(false), 2000);
  };

  return (
    <div className="w-full md:w-1/2 bg-[#0b1437] text-white rounded-xl shadow-lg overflow-hidden relative">
      {/* Tabs */}
      <div className="flex space-x-2 p-3 border-b border-white/10 bg-[#0e1a3a]">
        {snippets.map((tab, index) => (
          <button
            key={tab.label}
            onClick={() => setActiveTab(index)}
            className={`text-sm font-medium px-3 py-1 rounded-md transition-colors ${
              activeTab === index
                ? 'bg-white/10 text-white'
                : 'text-white/50 hover:text-white'
            }`}
          >
            {tab.label}
          </button>
        ))}
      </div>

      {/* Copy button */}
      <button
        onClick={copyCode}
        className="absolute top-3 right-4 flex items-center gap-1 px-3 py-1 text-xs bg-white/10 hover:bg-white/20 text-white rounded-md z-10"
      >
        {copied ? (
          <>
            <ClipboardCheck className="w-4 h-4" />
            Copi
          </>
        ) : (
          <>
            <Clipboard className="w-4 h-4" />
            Copier
          </>
        )}
      </button>

      {/* Code Viewer */}
      <div className="p-4 pt-2 overflow-x-auto">
        <SyntaxHighlighter
          language={currentCode.lang}
          style={vscDarkPlus}
          customStyle={{
            background: 'transparent',
            fontSize: '0.875rem',
            padding: 0,
            margin: 0,
            lineHeight: '1.6',
            borderRadius: '0.5rem',
          }}
          showLineNumbers
        >
          {currentCode.code}
        </SyntaxHighlighter>
      </div>
    </div>
  );
};

export default CodeBlockWithTabs;